// withPinLocation.js
import React, { useState, useRef, useEffect } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Typography,
  Button,
  Box,

} from "@mui/material";


import axios from "axios";
import { baseUrl } from "../baseUrl";
import toastMessage from "./toastMessage";

const CustomTextField = (props) => (
  <TextField
    variant="outlined"
    size="small"
   
    fullWidth
    sx={{
      "& .MuiOutlinedInput-root": {
        height: 42,
        "& .MuiOutlinedInput-input": {
          py: 1,
          textAlign:'center'
        },
        "&:hover fieldset": { borderColor: "var(--bs-primary1)" },
        "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" },
      },
    }}
    {...props}
  />
);

const withPinLocation = (Wrapped) => (props) => {
  const [open, setOpen]     = useState(false);
  const [pin, setPin]       = useState("");
  const [error, setError]   = useState("");
  const [coords, setCoords] = useState(null);
  const [loading , setLoading] = useState(false)

  // keeps the Promise resolvers waiting for coords
  const pendingResolvers = useRef([]);

  const ensureLocation = () =>
    new Promise((resolve, reject) => {
      // already have coords  fastpath
    //   if (coords) {
    //     resolve(coords);
    //     return;
    //   }
      // otherwise queue resolver and open dialog
      pendingResolvers.current.push({ resolve, reject });
      setOpen(true);
    });
    // const findLocation = async()=>{
      
    //       try {
    //         let res = await axios.get(`${baseUrl}user/-/get-user-mobile-location-pin`  ,{
    //             headers: {
    //                 "Content-type": "application/json; charset=UTF-8",
    //                 Authorization: `Bearer ${window.localStorage.getItem('userToken')}`,
    //             }})
    
    //           console.log(res , "res")
    //       } catch (error) {
            
    //       }
    //   }  

    //   useEffect(()=>{
    //     findLocation()
    //   },[])

  const getLocation = async()=>{
    let body = {
        pin : pin
    }
      try {
        let res = await axios.post(`${baseUrl}user/verify-mobile-location-pin` , body ,{
            headers: {
                "Content-type": "application/json; charset=UTF-8",
                Authorization: `Bearer ${window.localStorage.getItem('userToken')}`,
            }})

          return res?.data  
      } catch (error) {
        
      }
  }  

  const submitPin = async () => {
    if (!/^\d{4}$/.test(pin)) {
      setError("Enter a valid 4digit PIN");
      return;
    }
    setLoading(true)
    try {
      const fetched = await getLocation(); 
        if(fetched?.statusCode === 200)
        {
            setCoords(fetched.data);                 
            pendingResolvers.current.forEach(({ resolve }) => resolve(fetched.data));
            pendingResolvers.current = [];
          

            setTimeout(()=>{
                setOpen(false);
                setPin('')
            },100)
           
           
        }
        toastMessage(fetched?.error ,fetched?.message )
    
    
      setError("");
    } catch {
      setError("PIN not recognised or service unavailable");
    } finally {
        setLoading(false)
    }
  };

  const cancelPopup = ()=>{
    setCoords('');
    setOpen(false);
    setPin('')
  }

  const handleClose = () => {
    // user closed dialog without submitting  reject promises
    // pendingResolvers.current.forEach(({ reject }) =>
    //   reject(new Error("PIN dialog closed"))
    // );
    pendingResolvers.current = [];
    setOpen(false);
    setPin("");
    setError("");
  };

  return (
    <>
      <Wrapped {...props} ensureLocation={ensureLocation} />

      <Dialog open={open} onClose={handleClose}  fullWidth 
        PaperProps={{
            sx: () => ({
              borderRadius: 4,
              paddingY: 2,
              backdropFilter: "blur(10px)",
              background: "white",
              boxShadow: "0 8px 32px rgba(0, 0, 0, 0.2)",
              width: "100%",
              maxWidth: 350,
              //  overflow: tabs?.length === 0 ? 'hidden' : 'auto',
            }),
          }}
  
      
      >
        <DialogTitle 
          sx={{
            color: "var(--bs-primary1)",
            fontWeight: 600,
            textAlign: "center",
            fontSize: "12px",
          }}

        > Enter the 4digit PIN generated in your Android app.</DialogTitle>

<Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          flexDirection={"column"}
          mb={2}
          gap={1}
          marginX={4}
        >

              <CustomTextField
            fullWidth
            label="Enter You Location Pin "
            autoFocus
            name="userenterpin"
            value={pin}
            onChange={(e) =>
              setPin(e.target.value.replace(/\D/g, "").slice(0, 4))
            }
            error={!!error}
            helperText={error}
          />
          </Box>
          <DialogActions sx={{ justifyContent: "center", mt: 2 }}>
          <Box
            sx={{
              display: "flex",
              width: "100%",
              justifyContent: "flex-end",
              columnGap: 3,
              px: 3, // optional padding on sides
            }}
          >
            <Box>
              {/* <Button
                onClick={cancelPopup}
                color="error"
                variant="outlined"
                sx={{
                  borderRadius: 2,
                  px: 4,
                  width: "150px",
                }}
              >
                Cancel
              </Button> */}
            </Box>

            <Box>
              <Button
                disabled={pin.length !==4 || loading}
                color="primary"
                variant="contained"
                onClick={submitPin}
                sx={{
                  backgroundColor: "var(--bs-primary1)",
                  borderRadius: 2,
                  px: 4,
                  width: "150px",
                }}
              >
                Submit
              </Button>
            </Box>
          </Box>
        </DialogActions>

      </Dialog>
    </>
  );
};

export default withPinLocation;
