const useBankList = () => {
  const [bankList, setBankList] = useState([]);

  useEffect(() => {
    const fetchBanks = async () => {
      try {
        const res = await aepsBankList();
        const mapped = res?.data?.data?.map((item) => ({
          value: item, // Store full bank object as value
          label: item.bankName, // Show only bank_name in dropdown
        }));
        setBankList(mapped);
      } catch (error) {
        console.error("Error fetching bank list:", error);
      }
    };
    fetchBanks();
  }, []);

  return bankList;
};


const useDeviceFingerprintCapture = ({ensureLocation , withLocation = false}) => {
  const [selectedDeviceId, setSelectedDeviceId] = useState(null);

  const devices = [
    // { id: '1', img: imgg1, name: 'Iris' },
    { id: "2", img: imgg2, name: "Mantra" },
    { id: "3", img: imgg3, name: "Mantra L1" },
    { id: "4", img: imgg4, name: "Morpho" },
  ];

  const selectedDevice = devices.find((d) => d.id === selectedDeviceId);
  const wadhValue = ""; 
  const { captureFingerprint, loading } = useFingerprintCapture({ wadhValue });

  const handleDeviceSelect = (id) => {
    setSelectedDeviceId(id);
  };

  const capture = async () => {
    if (!selectedDevice) {
      toastMessage(true, "Please select a biometric device.");
      throw new Error("Device not selected");
    }

    try {
      // const { latitude  , longitude } = await ensureLocation();
      let latitude, longitude;
      if (withLocation ) {
        ({ latitude, longitude } = await ensureLocation());
      }
      const result = await captureFingerprint(selectedDevice.name);
      const xmlStr = result.pidData + "<bookz/>";
      const encodedXml = window.btoa(
        new XMLSerializer().serializeToString(
          new DOMParser().parseFromString(xmlStr, "application/xml")
        )
      
      );
      return {
        encodedXml,
        latitude,
        longitude,
        deviceName: selectedDevice.name,
      };
    } catch (err) {
      toastMessage(true, "Capture failed: " + err.message);
      throw err;
    }
  };

  return {
    devices,
    selectedDeviceId,
    handleDeviceSelect,
    capture,
    loading,
  };
};







 const aadharValidation = () =>
  Yup.string()
    .required("Aadhaar number is required")
    .matches(/^\d{12}$/, "Aadhaar number must be 12 digits");

const mobileValidation = () =>
    Yup.string()
    .required("Mobile Number is required.")
    .matches(/^[6-9]\d{9}$/, "Enter a valid mobile number.");   

const cashWithDrawSchema = Yup.object().shape({
      aadharNumber: aadharValidation(),
      mobile: mobileValidation(),
        bank_id: Yup.string().required("Please select a bank"),
        amount: Yup.string()
        .required("Amount is required.")
        .matches(/^\d+$/, "Amount must be numeric.")
        .test("min", "Min 100", (val) => parseInt(val || "0", 10) >= 100)
        .test("max", "Max 10000", (val) => parseInt(val || "0", 10) <= 10000),
    }); 
    
    const commonSchema = Yup.object().shape({
      aadharNumber: aadharValidation(),
      mobile: mobileValidation(),
        bank_id: Yup.string().required("Please select a bank"),
    });     

const CustomBox = ({ children }) => (
  <Box
    sx={{
      width: "100%",
      //   maxWidth: 800,
      mx: "auto",
      //   mt: 5,
      my: 1,
      px: 3,
      py: 4,
      pb: 6,
      //   bgcolor: '#F8F9FA',
      bgcolor: "white",
      borderRadius: 1,
      boxShadow: 0,
    }}
  >
    {children}
  </Box>
);

const CustomTextField = (props) => (
  <TextField
    variant="outlined"
    size="small"
    InputLabelProps={{
      shrink: true,
      //   style: { fontSize: '16px' },
    }}
    sx={{
      "& .MuiOutlinedInput-root": {
        height: 40,
        "& .MuiOutlinedInput-input": {
          py: 0,
        },
        "&:hover fieldset": { borderColor: "var(--bs-primary1)" },
        "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" },
        "& label.Mui-focused": {
          color: 'var(--bs-nav)',
        },
      },
    }}
    {...props}
  />
);

const BankSelection = () => {
  const {
    allAEPSProvider,
    setAllAEPSProvider,
    selectedAEPSProvider,
    setSelectedAEPSProvider,
    setAepsGlobalLoader,
    setAepsCurrentPage,
    triggerAuthApi,
  } = useAEPSService();
  const { service } = useParams();

  const getaepsVendor = async () => {
    let res = await getActiveaepsVendorList();
    const providers = res?.data?.data?.aepsApi || [];
    const defaultSlug = res?.data?.data?.defaultAepsApiObj?.slug;

    setAllAEPSProvider(providers);
    setSelectedAEPSProvider(defaultSlug);
  };

  const handleBankChange = (slug) => {
    setSelectedAEPSProvider(slug);
  };

  useEffect(() => {
    getaepsVendor();
  }, []);

  const handleOnboardingAuth = async () => {
    let body = {
      bank: selectedAEPSProvider,
    };
    if (service === "cash-deposit") {
      body.type = "cd";
    }
    setAepsGlobalLoader(true);
    try {
      let res = await getAepsAuth(body);
      if (res?.data?.data?.is_onboarding === false) {
        setAepsCurrentPage("onboarding-Page");
        return;
      }
      if (res?.data?.data?.dailyAuth === false) {
        setAepsCurrentPage("daily-Auth");
        return;
      }
      if (
        res?.data?.data?.is_onboarding === true &&
        res?.data?.data?.dailyAuth === true
      ) {
        setAepsCurrentPage("services");
        return;
      }
    } catch (error) {
    } finally {
      setAepsGlobalLoader(false);
    }
  };

  useEffect(() => {
    if (selectedAEPSProvider) {
      handleOnboardingAuth();
    }
  }, [selectedAEPSProvider , triggerAuthApi]);



  

  if(allAEPSProvider?.length === 0)
    {
      return <CustomBox>
        <p>Currently No Service Active</p>
      </CustomBox>
    }

  if(allAEPSProvider?.length === 1)
  {
    return null
  }

  return (
    <>
      <CustomBox>
        <Box display="flex" alignItems="center" gap={2} mb={2}>
          <Typography
            variant="body2"
            color="var(--bs-nav)"
            fontWeight="bold"
            fontSize={18}
          >
            Please Select Your Service
          </Typography>
        </Box>
        <Grid container spacing={2}>
          {allAEPSProvider?.map((bank) => {
            const isSelected = selectedAEPSProvider === bank.slug;

            return (
              <Grid item xs={8} sm={3} md={3} lg={2} key={bank.slug}>
                <Card
                  elevation={isSelected ? 6 : 1}
                  sx={{
                    border: isSelected
                      ? "1px solid var(--bs-nav)"
                      : "1px solid #ccc",
                    position: "relative",
                    borderRadius: 2,
                    transition: "all 0.3s ease",
                    width: "150px",
                  }}
                >
                  <CardActionArea onClick={() => handleBankChange(bank.slug)}>
                    <CardContent sx={{ p: 1 }}>
                      <Typography
                        variant="subtitle1"
                        align="center"
                        color="var(--bs-nav)"
                        fontSize={14}
                      >
                        {bank.slug.toUpperCase()}
                      </Typography>
                    </CardContent>
                    {isSelected && (
                      <CheckCircle
                        sx={{
                          position: "absolute",
                          top: 11,
                          right: 18,
                          width: "17px",
                          height: "17px",
                          color: "#1976d2",
                          fill: "var(--bs-nav)",
                        }}
                      />
                    )}
                  </CardActionArea>
                </Card>
              </Grid>
            );
          })}
        </Grid>
        {/* <Button
                variant="contained"
                color="primary"
                sx={{
                    height: 40,
                    borderColor: 'var(--bs-primary1)', // Custom colors
                    width: "200px" ,
                    backgroundColor: 'var(--bs-primary1)', // Custom colors
                    color: "white",
                    mt:3
                  }}
                  onClick={handleOnboarding}
              >
                Proced
              </Button> */}
      </CustomBox>
    </>
  );
};





const CheckDailyAuth = ({ ensureLocation }) => {
  const {
    devices,
    selectedDeviceId,
    setSelectedDeviceId,
    loading,
    handleDeviceSelect,
    capture,
  } = useDeviceFingerprintCapture({ ensureLocation , withLocation : true });
  const { service } = useParams();
  const { setDailyAuth, selectedAEPSProvider, setAepsCurrentPage } =
    useAEPSService();

  const handleCaptureFingerprint = async () => {
    try {
      const result = await capture();
      
      const body = {
        latitude: result.latitude,
        longitude: result.longitude,
        data: result.encodedXml,
        accessmodetype: "web",
        bank: selectedAEPSProvider,
      };
      console.log(body ,"body")
      if (service === "cash-deposit") {
        body.type = "cd";
      }

      const res = await getAepsDailyAuth(body);
      if (res?.data?.statusCode === 200  && res?.data?.error === false) {
        setDailyAuth(true);
        setAepsCurrentPage("services");
      }

      toastMessage(res?.data?.error, res?.data?.message);
    } catch (error) {
      console.error("Capture error:", error);
    }
  };

  return (
    <CustomBox>
      <Typography variant="body2" fontWeight="bold" fontSize={18} mb={2}>
        Complete Daily Verification
      </Typography>

      <Grid container spacing={2} sx={{ p: 2 }}>
        <Grid item xs={12}>
          <Grid container spacing={2}>
            {devices.map((device) => (
              <Grid item xs={6} sm={4} md={2} key={device.id}>
                <Card
                  onClick={() => handleDeviceSelect(device.id)}
                  sx={{
                    p: 1,
                    border:
                      selectedDeviceId === device.id
                        ? "2px solid var(--bs-nav)"
                        : "1px solid #ddd",
                    borderRadius: 2,
                    cursor: "pointer",
                  }}
                >
                  <CardMedia
                    component="img"
                    image={`${device.img}`}
                    alt={device.name}
                    sx={{ height: 80, objectFit: "contain" }}
                  />
                  <CardContent sx={{ p: 0 }}>
                    <Typography align="center">{device.name}</Typography>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Grid>

        <Grid item xs={12} textAlign="center">
          <Button
            variant="contained"
            disabled={!selectedDeviceId || loading}
            onClick={handleCaptureFingerprint}
            sx={{
              mt: 3,
              backgroundColor: "var(--bs-primary1)",
              color: "white",
            }}
          >
            {loading ? "Capturing..." : "Capture Fingerprint"}
          </Button>
        </Grid>
      </Grid>
    </CustomBox>
  );
};



const CashWithdrawal = ({ensureLocation}) => {
  const bankList = useBankList();
   const {setAepsGlobalLoader ,walletCall ,selectedAEPSProvider } =  useAEPSService()
  const {  devices, selectedDeviceId, handleDeviceSelect, capture, loading,} = useDeviceFingerprintCapture({ensureLocation});
 const [modalShow, setModalShow] = useState(false);
  const [dataModal, setDataModal] = useState(null);
  return (

    <>
  
    <Formik
      initialValues={{
        bank: null,
        bank_id: "",
        aadharNumber: "",
        mobile: "",
        amount: "",
      }}
      validationSchema={cashWithDrawSchema}
      onSubmit={async(values, { resetForm }) => {
       
        try {
          const result = await capture();
          const body = {
            accessmodetype:'web',
            latitude: result.latitude,
            longitude: result.longitude,
            data: result.encodedXml,
            nationalbankidentification:values.bank_id ,
            requestremarks:"cash/Addhar withdrawal",
            transactiontype:"CW",
            amount: values.amount,
            mobilenumber:values.mobile,
            adhaarnumber:values.aadharNumber,
            ipAddress:'',
            bank : selectedAEPSProvider
           
          };
           setAepsGlobalLoader(true)
          let res= await bankWithdraw(body)
           if(res?.data?.error  === false)
           {
            resetForm()
            handleDeviceSelect(null)
            setDataModal(res?.data);
            setModalShow(true);
            walletCall()
           }
          // toastMessage(false, "Cash Withdrawal request submitted!");
          toastMessage(res?.data?.error ,  res?.data?.message)  
        } catch (err) {
          // Already handled inside hook
        }
        finally {
          setAepsGlobalLoader(false)
        }
      }}
    >
      {({isValid,
    dirty, values, setFieldValue, touched, errors, validateField, setFieldTouched }) => (
        <Form>
          <Grid container spacing={4}>
            <Grid item xs={12} sm={6}>
              <Autocomplete
                options={bankList}
                getOptionLabel={(option) => option?.label || ""}
                value={values.bank}
                onChange={(e, selected) => {
                  setFieldValue("bank", selected || null);
                  setFieldValue("bank_id", selected?.value?.iinno || "");
                  setFieldTouched("bank_id", true);
                  setTimeout(() => validateField("bank_id"), 0);
                }}
                renderInput={(params) => (
                  <CustomTextField
                    {...params}
                    label="Select Bank"
                    fullWidth
                    variant="outlined"
                    error={touched.bank_id && Boolean(errors.bank_id)}
                    helperText={touched.bank_id && errors.bank_id}
                    InputLabelProps={{
                      sx: {
                        '&.MuiInputLabel-root:not(.MuiInputLabel-shrink)': {
                          top: '-6px',
                        },
                      },
                    }}
                  />
                )}
              />
            </Grid>

            <Grid item xs={12} sm={6}>
              <Field
                name="aadharNumber"
                as={CustomTextField}
                label="Aadhaar Number"
                fullWidth
                variant="outlined"
                inputProps={{ maxLength: 12 }}
                onChange={(e) => {
                  const value = e.target.value.replace(/\D/g, "").slice(0, 12);
                  setFieldValue("aadharNumber", value);
                }}
                error={touched.aadharNumber && Boolean(errors.aadharNumber)}
                helperText={touched.aadharNumber && errors.aadharNumber}
              />
            </Grid>

            <Grid item xs={12} sm={6}>
              <Field
                name="mobile"
                as={CustomTextField}
                label="Mobile Number"
                fullWidth
                variant="outlined"
                inputProps={{ maxLength: 10 }}
                onChange={(e) => {
                  const value = e.target.value.replace(/\D/g, "").slice(0, 10);
                  setFieldValue("mobile", value);
                }}
                error={touched.mobile && Boolean(errors.mobile)}
                helperText={touched.mobile && errors.mobile}
              />
            </Grid>

            <Grid item xs={12} sm={6}>
              <Field
                name="amount"
                as={CustomTextField}
                label="Amount"
                fullWidth
                variant="outlined"
                inputProps={{ maxLength: 7 }}
                onChange={(e) => {
                  const value = e.target.value.replace(/\D/g, "").slice(0, 7);
                  setFieldValue("amount", value);
                }}
                error={touched.amount && Boolean(errors.amount)}
                helperText={touched.amount && errors.amount}
              />
            </Grid>

            {/* Device Selection */}
            <Grid item xs={12}>
              <Grid container spacing={2}>
                {devices.map((device, i) => (
                  <Grid item xs={6} sm={4} md={2} key={i}>
                    <Card
                      onClick={() => handleDeviceSelect(device.id)}
                      sx={{
                        textAlign: "center",
                        p: 1,
                        cursor: "pointer",
                        border:
                          selectedDeviceId === device.id
                            ? "2px solid #1976d2"
                            : "1px solid #ccc",
                      }}
                    >
                      <CardMedia
                        component="img"
                        image={device.img}
                        alt={device.name}
                        sx={{ height: 80, objectFit: "contain" }}
                      />
                      <CardContent sx={{ p: 0 }}>
                        <Typography variant="body2">{device.name}</Typography>
                      </CardContent>
                    </Card>
                  </Grid>
                ))}
              </Grid>
            </Grid>

            {/* Submit Button */}
            <Grid item xs={12} textAlign="center">
              <Button
                type="submit"
                variant="contained"
                disabled={loading || !selectedDeviceId || !(isValid && dirty)}
                
                sx={{
                  mt: 3,
                  backgroundColor: "var(--bs-primary1)",
                  color: "white",
                }}
              >
                {loading ? "Capturing..." : "Capture & Withdraw"}
              </Button>
            </Grid>
          </Grid>
        </Form>
      )}
    </Formik>
    <AepsModalSucess
                show={modalShow}
                onHide={() => setModalShow(false)}
                adharPay="Aadhaar Withdrawal"
                dataModal={dataModal}
            />
    </>
  );

  

 
};

